@font-face {
  font-family: MyFont;
  src: url("./Sketchzone.otf");
}
@font-face {
  font-family: MyFont2;
  src: url("./kindergarten.ttf");
}
@font-face {
  font-family: MyFont3;
  src: url("./handwriting-draft_free-version.ttf");
}
@font-face {
  font-family: MyFont4;
  src: url("./SplashKhat-PERSONAL.otf");
}

.App {
  font-family: MyFont;
  font-size: 40px;
  overflow: hidden;
  text-align: center;
  display: grid;
  grid-gap: 10px;
  grid-template-areas:
    "H H "
    "L T ";
  grid-template-columns: 10% 65%;
  list-style: none;
  box-sizing: content-box;
}

.Header {
  grid-area: H;
  background: rgb(90, 99, 103);
}
.Lefter {
  grid-area: L;
  height: 100%;
  list-style: none;
}
.Content {
  grid-area: T;
  color: red;
}
