.Registration {
  display: block;
  border: 2px solid gray;
  box-sizing: border-box;
  border-radius: 10px;
  font-family: MyFont3;
  background: rgb(34, 32, 32);
  color: #ffa400;
}
.Reg {
  font-size: 28px;
}
.Reg2 {
  font-size: 28px;
  transform: translateX(-27px);
}
button {
  display: inline-block;
  padding: 10px 20px;
  border-radius: 4px;
  border: 1px solid black;
  color: rgb(39, 39, 39);
  margin-right: 15px;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 26px;
  font-family: "MyFont";
  box-shadow: 3px 3px 2px rgba(0, 0, 0, 0.7);
  background: #d6d6d6;
}
button:focus {
  outline: none;
}
button:active {
  transform: translateX(1px) translateY(1px);
  box-shadow: 3px 3px 2px rgba(0, 0, 0, 0);
}
button:disabled {
  background: #ccc;
  color: #000;
  cursor: not-allowed;
}
.error {
  background: rgba(238, 5, 16, 1);
}
.success {
  background: #affc41;
}
.primary {
  background: #ffa400;
  color: rgb(39, 39, 39);
}
.checkbox {
  font-size: 20px;
  font-family: MyFont3;
}
h1 {
  font-family: MyFont;
  color: #ffa400;
}
.errormessage {
  font-size: large;
  color: #fc4d41;
  position: absolute;
  transform: translateY(14px) translateX(5px);
}
