.Content2 {
  border: 2px solid gray;
  box-sizing: border-box;
  border-radius: 10px;
  font-family: MyFont3;
  background: rgb(34, 32, 32);
  display: grid;
  grid-gap: 10px;
  /* grid-template-rows: 280px 280px; 
  grid-template-columns: 50% 50%; */
  grid-template:
    "A I" auto
    "T T"
    "P P";
  /* = 1em / 20% 20px 1fr; */
  color: rgb(226, 27, 27);
}
