.Lefter {
  background: linear-gradient(90deg, rgb(185, 187, 197) 0%, #ffa400 50%);
  height: 100%;
  list-style: none;
  border: 2px solid gray;
  box-sizing: border-box;
  border-radius: 10px;
  font-family: serif;
}
ul {
  list-style-type: none;
  padding: 0px;
  margin: 0px;
}
a {
  text-decoration: none;
  color: black;
}
