.FindUsers {
  font-family: MyFont4;
  color: #c98291;
  background-color: #5a6367;
  border-radius: 10px;
  box-sizing: border-box;
  width: 65%;
  position: absolute;
  font-size: xx-large;
}
.User {
  top: 40px;
  border-radius: 10xp;
  border: 1px solid #6ba97a;
  margin: 40px;
  display: grid;
  grid-gap: 10px;

  grid-template-columns: 20% 80%;
}
.photo {
  border-radius: 100px;
  margin-top: 10px;
}
.img {
  border-radius: 100px;
  max-width: 70px;
  max-height: 70px;
}
.status {
  margin: 20px 0;
  font-family: MyFont;
  color: black;
}
.info {
  display: inline;
  margin: 10px;
  justify-content: space-between;
}
.name {
  margin-top: 20px;
  color: #e8ebf7;
  float: left;
}
.page {
  display: inline;
  cursor: pointer;
}
.selectedPage {
  font-weight: bold;
  color: black;
}
.active {
  border-radius: 50%;
  background-color: rgb(218, 87, 11);
  font-weight: bold;
}
.page li {
  display: inline;
}
