.Messages {
  border: 2px solid gray;
  box-sizing: border-box;
  border-radius: 10px;
  font-family: MyFont3;
  background: rgb(34, 32, 32);
  display: grid;
  grid-gap: 10px;

  grid-template-columns: 20% 80%;
  /* = 1em / 20% 20px 1fr; */
  color: rgb(226, 27, 27);
}
.dialog {
  border-right: 2px solid rgb(255, 255, 255);
}
.dialog a {
  color: crimson;

  font-size: xx-large;
}
a .active {
  color: green;
}
.messageWindow {
  color: darkgoldenrod;
  font-size: x-large;
  font-family: MyFont2;
}
